"use client";

import React, { useState, useEffect } from "react";
import ProjectSettings from "./ProjectSettings";
import ProjectList from "./ProjectList";

interface Project {
  id: string;
  name: string;
  offlineLabelingAllowed: boolean;
}

export default function AdminDashboard() {
  const [projects, setProjects] = useState<Project[]>([]);
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchProjects() {
      try {
        const res = await fetch("/api/projects");
        if (res.ok) {
          const data = await res.json();
          setProjects(data);
        } else {
          console.error("Failed to fetch projects");
        }
      } catch (err) {
        console.error("Error fetching projects:", err);
      } finally {
        setLoading(false);
      }
    }
    fetchProjects();
  }, []);

  const handleToggleOfflineLabeling = async (projectId: string, allowed: boolean) => {
    const updatedProjects = projects.map((project) =>
      project.id === projectId ? { ...project, offlineLabelingAllowed: allowed } : project
    );
    setProjects(updatedProjects);

    // Save updated project setting to backend
    try {
      const projectToUpdate = updatedProjects.find((p) => p.id === projectId);
      if (projectToUpdate) {
        const res = await fetch("/api/projects", {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(projectToUpdate),
        });
        if (!res.ok) {
          console.error("Failed to update project");
        }
      }
    } catch (err) {
      console.error("Error updating project:", err);
    }
  };

  if (loading) {
    return <div className="p-6 max-w-4xl mx-auto">Loading projects...</div>;
  }

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h2 className="text-2xl font-bold mb-6">Admin Dashboard</h2>
      <ProjectList projects={projects} onSelectProject={setSelectedProject} />
      {selectedProject && (
        <div className="mt-6">
          <h3 className="text-xl font-semibold mb-2">Settings for {selectedProject.name}</h3>
          <ProjectSettings
            offlineLabelingAllowed={selectedProject.offlineLabelingAllowed}
            onToggleOfflineLabeling={(allowed) =>
              handleToggleOfflineLabeling(selectedProject.id, allowed)
            }
          />
        </div>
      )}
    </div>
  );
}
