"use client";

import React from "react";

interface Project {
  id: string;
  name: string;
  offlineLabelingAllowed: boolean;
}

interface ProjectListProps {
  projects: Project[];
  onSelectProject: (project: Project) => void;
}

export default function ProjectList({ projects, onSelectProject }: ProjectListProps) {
  return (
    <div className="max-w-4xl mx-auto p-4 bg-gray-50 rounded-md shadow-md">
      <h3 className="text-xl font-semibold mb-4">Projects</h3>
      {projects.length === 0 ? (
        <p>No projects available.</p>
      ) : (
        <ul className="divide-y divide-gray-300">
          {projects.map((project) => (
            <li
              key={project.id}
              className="py-2 cursor-pointer hover:bg-gray-200 rounded px-2"
              onClick={() => onSelectProject(project)}
            >
              <div className="flex justify-between">
                <span className="font-medium">{project.name}</span>
                <span className="text-sm text-gray-600">
                  Offline Labeling: {project.offlineLabelingAllowed ? "Allowed" : "Not Allowed"}
                </span>
              </div>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
